
export default function Card(props){
    return(
        <div id={props.id} className="shadow-2xl flex justify-center ">
  <div className="flex justify-center items-center flex-col rounded-lg shadow-lg bg-white dark:bg-darkBgComponent max-w-sm">
    <a className="w-full min-h-[300px] flex flex-row items-center justify-center" target="_blank" href={props.url}>
      <img className="rounded-t-lg max-h-[300px] max-w-[300px]" src={"/images/"+ props.immagine} alt=""/>
    </a>
    <div className="p-6">
      <h5 className="dark:text-darkTextPrimary text-gray-900 text-xl font-medium mb-2">{props.titolo}</h5>
      <p className="dark:text-darkTextSecondary text-gray-700 text-base mb-4">
        {props.descrizione}
      </p>
      <p class="dark:text-darkTextPrimary text-gray-700 text-base mb-4">
        {"PREZZO: " + props.prezzo}
      </p>
      <a href={props.url} target="_blank" className=" inline-block px-6 py-2.5 bg-blue-600 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out">Visualizza prodotto</a>
    </div>
  </div>
</div>
    
    )
}