import logo from './logo.svg';
import './App.css';
import { detectAnyAdblocker } from 'just-detect-adblock';
import {useState, useEffect} from 'react';
import Header from './components/Header';
import BasePage from './components/BasePage';
import BaseSection from './components/BaseSection';
import Card from './components/Card';
function App() {
var data = require('./data.json')
  const [adb, setAdb] = useState(true)
  useEffect(() => {
    detectAnyAdblocker().then((detected) => {
      setAdb(detected)
    })
  },[])
  let i = [1,2,3,4,5,6,7,4,4,3,2,3,4,5]
  return (
    <div className="w-full">
      <Header />

      <BasePage>
      

        <section>
          <BaseSection>
            <div className='w-full flex flex-row flex-wrap gap-12 justify-center'> 

              {
                
                data.prodotti.map((item, index)=> {
                  return(
                    <Card id={item.id} url={item.url} immagine={item.immagine} titolo={item.titolo} prezzo={item.prezzo} descrizione={item.descrizione} />
                  )
                })
              }
             
            </div>
          </BaseSection>
          </section>



      </BasePage>

    </div>
  );
}

export default App;
