export default function Header(){
    const root = window.document.documentElement;

    function DarkMode(status){
        if(status){root.classList.remove('light'); root.classList.add('dark'); root.classList.add('bg-darkBg')}
        else{root.classList.add('light'); root.classList.remove('dark'); root.classList.remove('bg-darkBg')}
    }
    return(
        <div className="w-full bg-green-300 p-8 flex flex-row justify-between items-center">
            <div className="text-3xl font-bold">BRAND</div>
            <div className="cursor-pointer">
            <button className="dark:hidden block" onClick={() => DarkMode(true)}>
            <svg xmlns="http://www.w3.org/2000/svg" className="dark:hidden block h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={2}>
                <path strokeLinecap="round" strokeLinejoin="round" d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z" />
            </svg>
            </button>
            <button onClick={() => DarkMode(false)} className="dark:block hidden">
            <svg xmlns="http://www.w3.org/2000/svg" className="dark:block hidden h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor" stroke-width="2">
                <path stroke-linecap="round" stroke-linejoin="round" d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z" />
            </svg>
            </button>
            </div>
        </div>
    )
}